    font-size: 40px;
    text-align: center;
    color: #004225;
}

#restaurantList{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    padding: 20px;
  }
  
  #restaurantList > #restaurantContainer{
    /* border: 1px solid rgba(0, 0, 0, 0.8);
    background-color: rgba(255, 255, 255, 0.8);
    padding: 20px; */
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
    margin: 10px;
    padding: 5px;
    border-radius: 15px;
    background-color: #ededed;
  }

  #restaurantList > #restaurantContainer:hover{
    background-color: #FFCF9D;
  }
  
  #restaurantList > #restaurantContainer > #card{
    /* box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2); */
    padding: 10px;
    width: fit-content;
  }

  #restaurantList > #restaurantContainer > #card > a{
    text-decoration: none !important;
    color: inherit;
  }
  
  #restaurantList > #restaurantContainer > #card > a > .card-body h3{
    font-size: 25px;
    margin-bottom: 5px;
    color: #004225;
  }
  
  #restaurantList > #restaurantContainer > #card > a > .card-body p{
    font-weight: 100;
      display: -webkit-box;
      -webkit-line-clamp: 4;
      -webkit-box-orient: vertical;  
      overflow: hidden;
  }
  
  #restaurantList > #restaurantContainer > #card > a > .card-body img{
    width: 100%;
    border-radius: 15px;
    margin-bottom: 10px;
  }`, "",{"version":3,"sources":["webpack://./src/styles/favdetail.css"],"names":[],"mappings":"AAAA;IACI,eAAe;IACf,kBAAkB;IAClB,cAAc;AAClB;;AAEA;IACI,aAAa;IACb,qCAAqC;IACrC,aAAa;EACf;;EAEA;IACE;;oBAEgB;IAChB,0CAA0C;IAC1C,YAAY;IACZ,YAAY;IACZ,mBAAmB;IACnB,yBAAyB;EAC3B;;EAEA;IACE,yBAAyB;EAC3B;;EAEA;IACE,gDAAgD;IAChD,aAAa;IACb,kBAAkB;EACpB;;EAEA;IACE,gCAAgC;IAChC,cAAc;EAChB;;EAEA;IACE,eAAe;IACf,kBAAkB;IAClB,cAAc;EAChB;;EAEA;IACE,gBAAgB;MACd,oBAAoB;MACpB,qBAAqB;MACrB,4BAA4B;MAC5B,gBAAgB;EACpB;;EAEA;IACE,WAAW;IACX,mBAAmB;IACnB,mBAAmB;EACrB","sourcesContent":[".content__heading{\n    font-size: 40px;\n    text-align: center;\n    color: #004225;\n}\n\n#restaurantList{\n    display: grid;\n    grid-template-columns: repeat(3, 1fr);\n    padding: 20px;\n  }\n  \n  #restaurantList > #restaurantContainer{\n    /* border: 1px solid rgba(0, 0, 0, 0.8);\n    background-color: rgba(255, 255, 255, 0.8);\n    padding: 20px; */\n    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);\n    margin: 10px;\n    padding: 5px;\n    border-radius: 15px;\n    background-color: #ededed;\n  }\n\n  #restaurantList > #restaurantContainer:hover{\n    background-color: #FFCF9D;\n  }\n  \n  #restaurantList > #restaurantContainer > #card{\n    /* box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2); */\n    padding: 10px;\n    width: fit-content;\n  }\n\n  #restaurantList > #restaurantContainer > #card > a{\n    text-decoration: none !important;\n    color: inherit;\n  }\n  \n  #restaurantList > #restaurantContainer > #card > a > .card-body h3{\n    font-size: 25px;\n    margin-bottom: 5px;\n    color: #004225;\n  }\n  \n  #restaurantList > #restaurantContainer > #card > a > .card-body p{\n    font-weight: 100;\n      display: -webkit-box;\n      -webkit-line-clamp: 4;\n      -webkit-box-orient: vertical;  \n      overflow: hidden;\n  }\n  \n  #restaurantList > #restaurantContainer > #card > a > .card-body img{\n    width: 100%;\n    border-radius: 15px;\n    margin-bottom: 10px;\n  }"],"sourceRoot":""}]);
