    .restaurant .restaurant__review {
        grid-template-columns: repeat(1, 1fr);
        margin: 1rem;
    }

    .restaurant .restaurant__menu-food ul li p {
        font-size: 0.5rem;
        margin-right: 1rem;
    }

    .restaurant .restaurant__menu-drink ul li p {
        font-size: 0.5rem;
        margin-right: 1rem;
    }

    .restaurant__detail-info p {
        font-size: 0.7rem;
    }
}

@media (max-width: 499px){
    .restaurant__details{
        display: flex;
        flex-direction: column;
    }

    .restaurant__image img{
        width: 95%;
        height: 90%;
    }

    .restaurant__menu-food ul {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        margin-left: 2rem;
    }

    .restaurant__menu-food ul li p {
        font-size: 0.8rem;
        margin-right: 1.2rem;
    }

    .restaurant__menu-drink ul {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        margin-left: 2rem;
    }

    .restaurant__menu-drink ul li p {
        font-size: 0.8rem;
        margin-right: 1.2rem;
    }

    .restaurant__review {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        margin: 1rem;
    }
}

@media screen and (min-width: 500px) {
    .restaurant__details{
        display: flex;
        flex-direction: column;
    }

    .restaurant__image img{
        width: 95%;
        height: 90%;
    }

    .restaurant__menu-food ul {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        margin-left: 2rem;
    }

    .restaurant__menu-food ul li p {
        font-size: 0.8rem;
        margin-right: 1.2rem;
    }

    .restaurant__menu-drink ul {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        margin-left: 2rem;
    }

    .restaurant__menu-drink ul li p {
        font-size: 0.8rem;
        margin-right: 1.2rem;
    }

    .restaurant__review {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        margin: 1rem;
    }
}

@media screen and (min-width: 1000px) {
    .restaurant__details{
        display: flex;
        flex-direction: row;
    }

    .restaurant__image img{
        width: 650px;
        height: 450px;
    }

    .restaurant__menu-food ul {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        margin-left: 2rem;
    }

    .restaurant__menu-food ul li p {
        font-size: 0.9rem;
        margin-right: 1.2rem;
    }

    .restaurant__menu-drink ul {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        margin-left: 2rem;
    }

    .restaurant__menu-drink ul li p {
        font-size: 0.9rem;
        margin-right: 1.2rem;
    }

    .restaurant__review {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        margin: 1rem;
    }
}

@media screen and (min-width: 1200px) {
    
    main {
        max-width: 1300px;
